import {Injectable} from '@angular/core';
import portfolioItems from './mock-portfolio';

@Injectable()
export class PortfolioService {

    portfolioItems: Array<any> = portfolioItems;

  constructor() {
      console.log('mock invoices: ', portfolioItems);
      console.log(this.portfolioItems)
  }

  getInvoices() {
    return Promise.resolve(this.portfolioItems);
  }

}



