import { Component } from '@angular/core';
import { IonicPage, ModalController, NavParams, ViewController, NavController, AlertController, LoadingController, ToastController } from 'ionic-angular';
import { PortfolioService } from '../../providers/portfolio-service-mock';
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';

@IonicPage({
    name: 'page-investment-portfolio',
    segment: 'portfolioItems'
})

@Component({
    selector: 'page-investment-portfolio',
    templateUrl: 'investment-portfolio.html'
})

export class InvestmentPortfolioPage {

    portfolioItems: Array<any> = [];
    refresh:any;
    show: boolean = false;
    hide: boolean = true;
    selected = false;
    currentSelected: any;
    check: boolean;
    memberid:any;

    constructor(public navCtrl: NavController,public loadingCtrl: LoadingController,public translate: TranslateService,public modalCtrl: ModalController, public user: UserProvider, public service: PortfolioService) {
        this.translate.setDefaultLang('en');
        this.translate.use('es');
        this.memberid = JSON.parse(localStorage.getItem('MemberId'));
    }

    checkAllData() {
        this.user.userInfo().subscribe(
            data => {
                let datas = JSON.parse(JSON.stringify(data));
                let data2 = JSON.parse(datas._body);
                
                if (data2.success == false) {
                    // let toast = this.toastCtrl.create({
                    // 	showCloseButton: true,
                    // 	cssClass: 'profiles-bg',
                    // 	message: 'Your session expired due to an extended period of inactivity. Please log in again.',
                    // 	duration: 3000,
                    // 	position: 'top'
                    // });

                    // toast.onDidDismiss(() => {
                    console.log('Dismissed toast');
                    localStorage.removeItem('currentUser');
                    localStorage.removeItem('MemberId');
                    localStorage.removeItem("token");
                    localStorage.removeItem('2fa');
                    localStorage.removeItem('VerificationLevel');
                    localStorage.removeItem('chekout');
                    this.navCtrl.setRoot('page-auth');
                    // });

                    // toast.present();

                } else {



                }

            },error => {
				console.log(error.JSON);
				setTimeout(() => {
	      this.navCtrl.setRoot('page-general_error');
				  }, 1000);
			},
    )
    }

    openPropertyListPage(item) {
        console.log('itemTapped: ', item);
        // this.navCtrl.push(InvoiceDetailPage, invoice);
        this.navCtrl.push('page-invest-checkout', { "item": item });
    }


    ionViewDidEnter(){
        this.getInvoices();

    }

    getInvoices() {

        this.translate.get('my-wallet-page.refreshing').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'

            // You can call instant() here
            this.refresh = this.translate.instant('my-wallet-page.refreshing');

        })

        let loading = this.loadingCtrl.create({
            content: this.refresh
        });

        loading.present();

        this.user.finance().subscribe(
            data => {
                let datas = JSON.parse(JSON.stringify(data));
                let data2 = JSON.parse(datas._body);
                this.check = this.user.checkDataEmpty(data2.data);
                console.log(this.check);
                if (this.check) {
                    this.navCtrl.setRoot('page-general_error');

                } else {
                    this.portfolioItems = data2.data;
                    if (this.portfolioItems.length === 0) {
                        this.hide = false;
                        console.log(this.hide);
                    } else {
                        this.hide = true;
                    }
                    console.log(data2.data);
                }

            },error => {
				console.log(error.JSON);
				setTimeout(() => {
					this.navCtrl.setRoot('page-general_error');
				  }, 1000);
            },
            () => {


                loading.dismiss();


            }

        )

        //   this.service.getInvoices().then(data => this.portfolioItems = data);
    }


    ngOnInit() {
        this.checkAllData();
        // this.getInvoices();
        // console.log("invoice data: " + this.portfolioItems);
    }

    Tap(item, idx) {

        this.show = true;
        console.log(item);
        this.currentSelected = idx;

        this.selected = true;
    }

    // buy() {
    //     let profileModal = this.modalCtrl.create(ModalPage1, { Pair: 'SHF', Amount: '100.00', Price: '0.00',UID:'034242' });
    //     profileModal.present();

    // }

    sell(item) {
        this.navCtrl.push('page-sales-create',{item:item});

    }
    profile(LegalId) {

        this.navCtrl.push('page-marketing', {
			'legalId': LegalId
		});
     
    }

    // go to front list page

    Browse() {
        this.navCtrl.setRoot('page-home');
    }

    // go to deposit page
    depositmoney() {

        this.navCtrl.push('page-add-amount');

    }


}



